.SceneContainer {
    display: flex;
    height: 100vh;
    width: 100vw;
    align-items: center;
    justify-content: center;
    position: absolute;
    top: 0;
    left: 0;
    background-image: url("../images/01.gif");
    background-position: bottom;
    background-size: cover;
    background-repeat: no-repeat;
    overflow: hidden;
}

.StageBG {
    height: 100%;
    width: 100%;
    background-image: url("../images/stage_bg.png");
    background-position: bottom;
    background-size: cover;
    background-repeat: no-repeat;
    position: absolute;
}

.TextContainer {
    display: flex;
    height: 100%;
    width: 100%;
    position: absolute;
}

.CurvedTextContainer {
    display: flex;
    position: absolute;
    width: 100%;
    align-items: end;
    justify-content: center;
    font-size: 15vh;
    color: white;
    font-weight: 700;
    animation-name: entry;
    animation-duration: 4s;
    animation-timing-function: ease-out;
    animation-iteration-count: 1;   
    animation-fill-mode: both;  
}

.MainTextContainer {
    display: flex;
    position: absolute;
    height: 50%;
    width: 35%;
    align-items: center;
    justify-content: center;
    text-align: center;
    font-size: 50vh;
    font-weight: 100;
    color: white;
    font-style: oblique;
    animation-name: entry;
    animation-duration: 2s;
    animation-timing-function: ease-out;
    animation-iteration-count: 1;
    animation-fill-mode: both;
}

.Right {
    bottom: 12vh;
    right: 0;
}

.Left {
    bottom: 12vh;
    left: 0;
}

.LowOpacity {
    opacity: 0.1 !important;
}

@keyframes entry {
    0% {scale: 1.2;opacity: 0;color: #9dffe5;}
    5% {opacity: 1;}
    10% {scale: 0.75;opacity: 1;}
    50% {color: #ffbcf9;}
    100% {opacity: 0;color: white;}
}