.App {
  text-align: center;
  background-color: #ffffff;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
}

iframe#webpack-dev-server-client-overlay {
  display: none;
}

.Container {
  display: flex;
  height: 100vh;
  width: 100%;
  flex-direction: column;
  overflow: hidden;
}

.HomeButtonContainer {
  display: flex;
  background-color: white;
  width: 30vw;
  height: 39vh;
  border-radius: 25px;
  position: fixed;
  right: 10vh;
  bottom: 2vh;
  box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.35);
  align-items: center;
  justify-content: center;
  flex-direction: column;
  gap: 2vh;
}

.MobileErrorMessage {
  display: flex;
  align-items: center;
  justify-content: center;
  color: #2069c9;
  font-size: 20px;
  box-sizing: border-box;
  padding: 24px;
  gap: 5px;
  font-weight: 600;
}

.TopBanner {
  display: flex;
  height: 6vh;
  width: 100%;
  align-items: end;
  justify-content: center;
  background-color: #2069c9;
  color: aliceblue;
  font-size: 3.9vh;
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
  font-weight: 900;
  z-index: 0;
}

.TitleText {
  display: flex;
  position: fixed;
  top: 15vh;
  left: 10vh;
  height: 30vh;
  width: 25vw;
  background-color: white;
  background-image: url("../src/images/Title_Text.jpg");
  background-position: center;
  background-size: contain;
  background-repeat: no-repeat;
  border-radius: 25px;
  box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.35);
}

.MakuhariSilhouette {
  display: flex;
  position: fixed;
  height: 90vmax;
  width: 90vmax;
  max-height: 110vh;
  max-width: 110vh;
  bottom: -25vh;
  right: 20vmax;
  background-image: url("../src/images/makuhari_alpha.png");
  background-position: center;
  background-size: contain;
  background-repeat: no-repeat;
  rotate: -15deg;
  animation-name: float1;
  animation-duration: 20s;
  animation-timing-function: ease-in-out;
  animation-delay: 0s;
  animation-iteration-count: infinite;
}

.Note1 {
  display: flex;
  position: fixed;
  height: 30vmax;
  width: 30vmax;
  max-height: 100vh;
  max-width: 100vh;
  top: -15vh;
  right: 0vmax;
  background-image: url("../src/images/note1.png");
  background-position: center;
  background-size: contain;
  background-repeat: no-repeat;
  rotate: -15deg;
  animation-name: float1;
  animation-duration: 20s;
  animation-timing-function: ease-in-out;
  animation-delay: -7s;
  animation-iteration-count: infinite;
}

.Note2 {
  display: flex;
  position: fixed;
  height: 60vmax;
  width: 60vmax;
  max-height: 100vh;
  max-width: 100vh;
  bottom: -15vh;
  left: -10vmax;
  background-image: url("../src/images/note2.png");
  background-position: center;
  background-size: contain;
  background-repeat: no-repeat;
  rotate: -15deg;
  animation-delay: -12s;
}

.InfoIcon {
  display: flex;
  aspect-ratio: 1;
  width: 64px;
  background-image: url("../src/images/info.png");
  background-position: center;
  background-size: contain;
  background-repeat: no-repeat;
}

.ARInfoContainer {
  display: flex;
  height: 100%;
  width: 100%;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  gap: 15px;
  padding: 30px;
  box-sizing: border-box;
  color: #2069c9;
  font-size: 20px;
}

.Float {
  animation-name: float1;
  animation-duration: 20s;
  animation-timing-function: ease-in-out;
  animation-iteration-count: infinite;
}

@keyframes float1 {
  0% {transform: translateY(0px); rotate: -15deg;}
  50% {transform: translate(-25px, 50px); rotate: -18deg;}
  100% {transform: translateY(0px); rotate: -15deg;}
}
