.StageBackButton {
    display: flex;
    position: absolute;
    top: 2vh;
    left: 1vw;
    background-color: white;
    color: rgb(1, 59, 72);
    transition-duration: 0.2s;
    font-size: 2vh;
    user-select: none;
    padding: 10px 15px 10px 15px;
    border-radius: 5px; 
}

.StageBackButton:hover {
    scale: 1.1;
}

.StageBackButton:active {
    scale: 1;
}