.InstructionsContainer {
    display: flex;
    flex-direction: row;
    align-items: start;
    justify-content: center;
    position: fixed;
    height: 90vh;
    width: 75vw;
    top: 5vh;
    left: 12.5vw;
    background-color: white;
    border-radius: 25px;
    overflow: hidden;
}

.VerticalDividerThird {
    display: flex;
    width: 30%;
    height: 80%;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.PhoneBG {
    background-image: url("../images/phone.png");
    background-position: center;
    background-size: contain;
    background-repeat: no-repeat;
}

.Instructions {
    display: flex;
    height: 100%;
    width: 100%;
    flex-direction: column;
    gap: 15px;
    align-items: center;
    justify-content: center;
    color: #2069c9;
    font-weight: 600;
}

.InstructionsTitle {
    font-size: 32px;
    margin-top: 30px;
}

.InstructionsContent {
    font-size: 20px;
}

.CountDown {
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    height: 20%;
    width: 100%;
    position: absolute;
    bottom: 0vh;
    left: 0vw;
    color: #2069c9;
    font-size: 7vh;
    font-weight: lighter;
    font-family: monospace;
}

.QRCodeContainer {
    display: flex;
    width: 30%;
    height: 100%;
    aspect-ratio: 1;
    align-items: center;
    justify-content: center;
}

.Scan {
    display: flex;
    height: 50%;
    aspect-ratio: 1;
    background-image: url("../images/scan.png");
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center;
    rotate: 10deg;
}

.View {
    display: flex;
    height: 50%;
    aspect-ratio: 1;
    background-image: url("../images/view.png");
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center;
    animation-delay: -4s;
    rotate: -10deg;
}

.Marker {
    display: flex;
    height: 25vh;
    width: 20vw;
    background-color: white;
    border-radius: 25px;
    background-image: url("../images/markers/pattern-makuhari_black.png");
    background-size: contain;
    background-position: center;
    background-repeat: no-repeat;
}

.Float {
    animation-name: float;
    animation-duration: 5s;
    animation-fill-mode: both;
    animation-timing-function: ease-in-out;
    animation-iteration-count: infinite;
}

.Show {
    animation-name: show;
    animation-duration: 0.39s;
    animation-fill-mode: both;
    animation-timing-function: ease-in-out;
    animation-iteration-count: 1;
}

.Hide {
    animation-name: hide;
    animation-duration: 0.39s;
    animation-fill-mode: both;
    animation-timing-function: ease-in-out;
    animation-iteration-count: 1;
}

@keyframes float {
    0% { translate: 0px 20px; }
    50% { translate: 0px 10px; }
    100% { translate: 0px 20px; }
}

@keyframes hide {
    0% { scale: 1; opacity: 1; }
    100% { scale: 0.9; opacity: 0; }
}

@keyframes show {
    100% { scale: 1; opacity: 1; }
    0% { scale: 0.9; opacity: 0; }
}